import React, { Component } from 'react'
import axios from "axios";
import { Spring, Transition, animated } from 'react-spring/renderprops';
import { Col, Container, Row } from "react-bootstrap";
import CurrencyFormat from 'react-currency-format';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faEye, faRedoAlt } from '@fortawesome/free-solid-svg-icons'
import moment from 'moment'

export default class ComercesPage extends Component {
    constructor(props) {
        super(props)
        this.state = {
            cop_cash: 0,
            buyers: {},
            sellers: {},
            rates: [],
            buyersFinished: false,
            sellersFinished: false
        }
    }

    componentDidMount() {
        this.getBuy(1);
        this.getSell(1);
    }

    shouldComponentUpdate(nextProps, nextState) {
        console.log(nextState);
        
        return true
    }

    getBuy = (page) => {
        let stateBuyers = {}
        axios.get("https://brokechain-api-node.herokuapp.com/show-buy-bitcoins-online-cop/" + page)
        .then(response => {
            // console.log(response.data.data.ad_list, page)
            if(Object.entries(this.state.buyers).length === 0) {
                this.setState({buyers: response.data})
            } else {
                stateBuyers = Object.assign({}, this.state.buyers)
                let stateAdList = this.state.buyers.data.ad_list
                let ad_list = stateAdList.concat(response.data.data.ad_list)
                stateBuyers.data.ad_list = ad_list
                this.setState({buyers: stateBuyers})
            }
            if (response.data.hasOwnProperty("pagination")) {
                if (response.data.pagination.hasOwnProperty("next")) {
                    return this.getBuy(++page);
                } else {
                    this.setState({buyersFinished: true})
                }
            }
        }).catch(err => alert(err));
    }

    getSell = (page) => {
        let stateSellers = {}
        axios.get("https://brokechain-api-node.herokuapp.com/show-sell-bitcoins-online-ves/" + page)
        .then(response => {
            if(Object.entries(this.state.sellers).length === 0) {
                this.setState({sellers: response.data})
            } else {
                stateSellers = Object.assign({}, this.state.sellers)
                let stateAdList = this.state.sellers.data.ad_list
                let ad_list = stateAdList.concat(response.data.data.ad_list)
                stateSellers.data.ad_list = ad_list
                this.setState({sellers: stateSellers})
            }
            if (response.data.hasOwnProperty("pagination")) {
                if (response.data.pagination.hasOwnProperty("next")) {
                    this.getSell(++page);
                } else {
                    this.setState({sellersFinished: true})
                }
            }
        }).catch(err => alert(err));
    }

    compareCommerces = () => {
        let buyers = this.state.buyers.data.ad_list
            .filter(element => (element.data.min_amount <= this.state.cop_cash || element.data.min_amount === null) && (element.data.max_amount >= this.state.cop_cash || element.data.max_amount === null) )
            .sort((a,b) => {
                if (a.data.temp_price > b.data.temp_price) {
                    return -1;
                }
                if (a.data.temp_price < b.data.temp_price) {
                    return 1;
                }
                return 0;
            })

        let sellers = this.state.sellers.data.ad_list

        let rates = []

        let id = 0;
        for (let i = 0; i < buyers.length; i++) {
            let buyer = buyers[i]
            id++
            for (let j = 0; j < sellers.length; j++) {
                let seller = sellers[j]
                let btc = this.state.cop_cash / buyer.data.temp_price
                let ves = btc * seller.data.temp_price
                if( (parseFloat(seller.data.min_amount) < ves || seller.data.min_amount === null) && (parseFloat(seller.data.max_amount) > ves || seller.data.max_amount === null)) {
                    let data = {id, buyer, seller, cop: this.state.cop_cash, btc, ves,
                        rate: this.state.cop_cash / ves, isShown: false
                    }
                    rates.push(data)
                }
                id++
            }
        }

        this.setState({rates});
    }

    render() {
        return (
            <Container className="commerces">
                <Row>
                    <Col xs={2} className="mt-3">
                        <button 
                            onClick={() => {this.setState({buyers: {}, sellers: {}, buyersFinished: false, sellersFinished: false}); this.getBuy(1); this.getSell(2); }}
                        className="btn btn-primary">
                            <FontAwesomeIcon icon={faRedoAlt} className="icon" />
                        </button>
                    </Col>
                    <Col xs={10} className="mt-3">
                        <div className="input-group">
                            <div className="input-group-prepend">
                                <span className="input-group-text bg-primary text-white">COP</span>
                            </div>
                            <CurrencyFormat className="form-control" thousandSeparator={","} decimalSeparator={"."} allowNegative={false} 
                                onChange={ e => {
                                    const valor = e.target.value.replace(/,/g, "")
                                    this.setState({cop_cash: parseFloat(valor), rates: []})
                                }}
                            />
                        </div>
                    </Col>
                    <Col xs={12} className="mt-3">
                        <div className="input-group">
                            <button className="btn btn-secondary btn-block" 
                                onClick={this.compareCommerces}
                                disabled={
                                    this.state.cop_cash === "" || this.state.cop_cash === "0" || this.state.cop_cash === 0 || this.state.cop_cash < 0.000000000000000001 ||
                                    isNaN(this.state.cop_cash) || this.state.buyersFinished === false || this.state.sellersFinished === false
                                }
                            >
                                {this.state.buyersFinished === false || this.state.sellersFinished === false ? "Cargando comercios..." : "Buscar comercios"}
                            </button>
                        </div>
                    </Col>
                    <Col xs={12} className="mt-3">
                        <Container fluid={true}>
                            <Row>
                                {this.state.rates.sort((a,b) => {
                                    if (a.rate < b.rate) {
                                        return -1;
                                    }
                                    if (a.rate > b.rate) {
                                        return 1;
                                    }
                                    return 0;
                                }).filter((item, index) => index <= 200)
                                .map((rate, index) => (
                                    <Col key={index} xs={12} className="mt-1">
                                        <div className="rateResult">
                                            <div className="flex">
                                                <div className="m-1">
                                                    <button onClick={() => {
                                                        let rates = [...this.state.rates];
                                                        rates.forEach(element => {
                                                            if (rate.id === element.id) {
                                                                rate.isShown  = !rate.isShown
                                                            }
                                                        })
                                                        this.setState({rates})
                                                    }} className={`btn btn-${
                                                        (moment(rate.seller.data.profile.last_online).fromNow(true).includes("minute") || moment(rate.seller.data.profile.last_online).fromNow(true).includes("second")) &&
                                                        (moment(rate.buyer.data.profile.last_online).fromNow(true).includes("minute") || moment(rate.buyer.data.profile.last_online).fromNow(true).includes("second")) ?
                                                        "primary" : "danger"
                                                    }`}
                                                    >
                                                        <FontAwesomeIcon icon={faEye} className="icon" />
                                                    </button>
                                                </div>
                                                <div className="content">
                                                    <div>
                                                        <span className="title">Tasa: </span>
                                                        <span className="value"><CurrencyFormat value={rate.rate} displayType={'text'} thousandSeparator={","} decimalSeparator={"."} /></span>
                                                    </div>
                                                    <div>
                                                        <span className="title">Compra COP: </span>
                                                        <a href={rate.buyer.actions.public_view} rel="noopener noreferrer" target="_blank">{rate.buyer.data.profile.username}</a>
                                                        <br/>
                                                        <span className="title">Venta VES: </span>
                                                        <a href={rate.seller.actions.public_view} rel="noopener noreferrer" target="_blank">{rate.seller.data.profile.username}</a>
                                                    </div>
                                                </div>
                                            </div>
                                                <Transition
                                                    native
                                                    items={rate.isShown}
                                                    from={{ opacity: 0, transform: "translateY(30px)" }}
                                                    enter={{ opacity: 1, transform: "translateY(0px)" }}
                                                    leave={{ opacity: 0, transform: "translateY(30px)" }}
                                                >
                                                    {show => show && (props => (
                                                        <animated.div style={props}>
                                                            <Spring from={{opacity: 0}} to={{opacity: 1}} >
                                                                {props => (
                                                                    <div style={props} className="result">
                                                                        <div>
                                                                            <span className="title">BTC: </span>
                                                                            {rate.btc.toFixed(8)}
                                                                        </div>
                                                                        <div>
                                                                            <span className="title">VES: </span>
                                                                            {rate.ves.toFixed(2)}
                                                                        </div>
                                                                        <div>
                                                                            <span className="title">Compra COP: </span>
                                                                            <a href={`https://localbitcoins.com/accounts/profile/${rate.buyer.data.profile.username}/`} rel="noopener noreferrer" target="_blank">{rate.buyer.data.profile.username}</a>
                                                                        </div>
                                                                        <div>
                                                                            <span className="title">Venta VES: </span>
                                                                            <a href={`https://localbitcoins.com/accounts/profile/${rate.seller.data.profile.username}/`} rel="noopener noreferrer" target="_blank">{rate.seller.data.profile.username}</a>
                                                                        </div>
                                                                        <div>
                                                                            <span className="title">Comprador COP hace: </span>
                                                                            {moment(rate.seller.data.profile.last_online).fromNow(true)}
                                                                        </div>
                                                                        <div>
                                                                            <span className="title">Vendedor VES hace: </span>
                                                                            {moment(rate.buyer.data.profile.last_online).fromNow(true)}
                                                                        </div>
                                                                    </div>
                                                                )}
                                                            </Spring>
                                                        </animated.div>
                                                    ))}
                                                </Transition>
                                        </div>                 
                                    </Col>
                                ))}
                            </Row>
                        </Container>
                    </Col>
                </Row>
            </Container>
        )
    }
}
