import React, { Component } from 'react'
import { Container, Row, Col } from 'react-bootstrap'
import htmlToImage from 'html-to-image';
import ratesImage from '../../images/background-rates-2.jpg'

export default class RateImagePage extends Component {
    constructor(props) {
        super(props)
        this.state = {
            wasGenerated: false
        }
    }

    generateImage = () => {
        var node = document.getElementById('contenedor');

        this.setState({wasGenerated: true})
 
        htmlToImage.toPng(node)
        .then(function (dataUrl) {
            var img = new Image();
            img.src = dataUrl;
            // document.body.appendChild(img);
            document.getElementById("ready-imagen").appendChild(img)
            
        })
        .catch(function (error) {
            console.error('oops, something went wrong!', error);
        });
    }

    render() {
        return (
            <Container className="rate-image">
                <Row>
                    <Col xs={12}>
                        <button onClick={this.generateImage} className="btn btn-primary">Generar</button>
                        {this.state.wasGenerated ? (<div>GENERADO</div>) : ""}
                        <input type="text" className="form-control" />
                    </Col>
                    <Col xs={12} id="ready-imagen">
                    </Col>
                    <Col xs={12}>
                        <div id="contenedor" className="contenedor">
                            <img src={ratesImage} alt="ratesImage" className="image" width="390" />
                            <div className="title">TASAS DEL DÍA</div>
                        </div>
                    </Col>
                </Row>
            </Container>
        )
    }
}
